import { IsEmail, IsString } from 'class-validator';

export class PreSignUpDto {
  @IsEmail()
  public email: string;
}
export class CreateUserDto {
  @IsEmail()
  public email: string;

  @IsString()
  public password: string;

  @IsString()
  public firstName: string;

  @IsString()
  public lastName: string;
}
export class LoginDto {
  @IsEmail()
  public email: string;

  @IsString()
  public password: string;
}
